// 
import React, { Component } from 'react';
import { observable, action } from 'mobx';
import { inject, observer } from 'mobx-react';
import classNames from 'classnames';
import { Link } from 'react-router-dom';
import { Form, Input, Icon, Checkbox, Button, message } from 'antd';
import QueueAnim from 'rc-queue-anim';

// utils 
import {fPromise} from '../../../../utils/f2f';

// 
import styles from './style.module.less';

// 
const FormItem = Form.Item;

@Form.create()
@observer
class LoginForm extends Component {
  // 
  @observable loading = false;

  // 
  @action setLoading = (loading = false) => {
    this.loading = loading;
  }

  // 
  @action handleSubmit = (e) => {
    e.preventDefault();
    // 
    this.props.form.validateFields((err, values) => {
      if (!err) {
        // 
        this.setLoading(true);
        console.log(': ', values);
        // mst
        this.props.login(values).then((res)=>{
          message.success('');
          this.setLoading(false);
        }).catch(()=>{
          this.setLoading(false);
        });
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <div className={ classNames(styles.loginFormPanel, this.props.className, 'clearfix') } style={ this.props.style }>
        {/*  */ }
        <QueueAnim type="scale" interval={ 0 }>
          <h1 key="a" className={ styles.title }></h1>
          <Form key="b" onSubmit={ this.handleSubmit }>
            <FormItem>
              { getFieldDecorator('username', {
                rules: [{ required: true, message: '' }],
              })(
                <Input className={ styles.input } prefix={ <Icon type="user" /> } placeholder="" />
              ) }
            </FormItem>
            <FormItem>
              { getFieldDecorator('password', {
                rules: [{ required: true, message: '' }],
              })(
                <Input className={ styles.input } prefix={ <Icon type="lock" /> } type="password" placeholder="" />
              ) }
            </FormItem>
            <FormItem>
              { getFieldDecorator('ifRemember', {
                valuePropName: 'checked',
                initialValue: true,
              })(
                <Checkbox className={ styles.checkbox }></Checkbox>
              ) }
              <Link className={ styles.textLink } to="/"></Link>
            </FormItem>
            <FormItem>
              <div className={ styles.handleRow }>
                <div className={ styles.handleButtonCol }>
                  <Button block loading={ this.loading } className={ styles.handleButton } ghost type="primary" htmlType="submit"></Button>
                </div>
                <Link className={ styles.textLink } to="/register"></Link>
              </div>
            </FormItem>
          </Form>
          <p key="c"></p>
        </QueueAnim>
      </div>
    );
  }
}

LoginForm.defaultProps = {
  // ,
  login: fPromise,
};

const InjectLoginForm = inject(({ authStore = {} }) => ({
  login: authStore.login,
  logged: authStore.logged,
}))(LoginForm);

export {
  LoginForm,
  InjectLoginForm
};


